/**
 *  ,     
 * @property level -  ,  1  4
 * @property attack -  
 * @property defence -  
 * @property health -  
 * @property type -      :
 * swordsman
 * bowman
 * magician
 * daemon
 * undead
 * vampire
 */
export default class Character {
  constructor(level, type = 'generic') {
    if (new.target.name == 'Character') {
      throw new Error('    Character!');
    }

    this.level = level > 4 ? 4 : level;
    this.attack = 0;
    this.defence = 0;
    this.health = 50;
    this.type = type;
    this.rangeMov;      //  
    this.rangeAttack;   //  

    if (this.level > 1) {
      for (let i = 2; i <= this.level; i++) {
        this.levelUp();
      }
    }


    // TODO:  ,  -  "new Character()"
  }

  levelUp() {
    const healthBefore = this.health;
    this.health += 80;

    if (this.health > 100) {
      this.health = 100;
    }
    this.attack = Math.max(this.attack, this.attack * ((1.8 - healthBefore) / 100));
    this.defence = Math.max(this.defence, this.defence * ((1.8 - healthBefore) / 100));
  }

}
